
import { defineComponent, ref } from "vue";

export default defineComponent({
  name: "base-input",
  emits: ["input"],
  props: {
    placeholder: {
      type: String,
      default: "Enter text here",
    },
    value: {
      type: String,
    },
    id: {
      type: String,
    },
    inputType: {
      type: String,
      default: 'text'
    },
  },
  setup(props, { emit }) {
    const inputVal = ref(props.value);
    function handleInput() {
      emit("input", inputVal.value);
    }
    return { inputVal, handleInput };
  },
});
