
import { defineComponent } from "vue";

export default defineComponent({
  name: "base-button",
  emits: ["btnClick"],
  props: {
    title: {
      type: String,
      default: "Click",
    },
  },
  setup(_, { emit }) {
    function handleClick() {
      emit("btnClick");
    }
    return { handleClick };
  },
});
